import React from 'react'

import "./ScoreCell.css"

export default function ScoreCell(props) {
  return (
      <td
        // className={`player${props.player} ${(parseInt(props.score) < 0) ? "curr-hand-winner" : ""}`}
        className={`player${props.player} score-cell`}
      >
        <input
          type="text"
          // min="0"
          // inputMode="numeric"
          // pattern="[0-9]*"
          name=""
          data-i={props.i}
          data-j={props.j}
          id={`player${props.player}-hand${props.hand}-score`}
          value={props.score}
          onChange={props.handleRowScores}
          onBlur={props.handleCellOnBlur}
          className={(parseInt(props.score) < 0) ? "curr-hand-winner" : ""}
        />
      </td>
  )
}
