import React from 'react';
import ScoreCell from '../ScoreCell/ScoreCell';

import Broom from '../Broom/Broom';

import "./ScoreRow.css";

export default function ScoreRow(props) {
  return (
    <tr className="score-row">
      <td>
        <div className="hand">
          {props.hand}
        </div>
      </td>
      {props.row_scores.map((score, index) =>
          <ScoreCell
            key={index}
            score={score}
            player={index + 1}
            index={index}
            hand={props.hand}
            allScores={props.allScores}
            i={props.i}
            j={index}
            handleRowScores={props.handleRowScores}
            handleCellOnBlur={props.handleCellOnBlur}
          />
      )}
      <td>
          <button className="btn btn-delete-row">
              <Broom />
          </button>
      </td>
    </tr>
  )
}
