import React, {useEffect, useState} from "react";

import "./Scores.css";

import ScoreRow from "../ScoreRow/ScoreRow";
import Trash from "../Trash/Trash";
// import ChevronDown from "../Chevron-Down/ChevronDown";
import Broom from "../Broom/Broom";
import Plus from "../Plus/Plus";
import PlusSquare from "../PlusSquare/PlusSquare";
// import Trophy from "../Trophy/Trophy";
import Medal from "../Medal/Medal";
// import Clubs from "../Clubs/Clubs";
// import Diamonds from "../Diamonds/Diamonds";
// import Hearts from "../Hearts/Hearts";
// import Spades from "../Spades/Spades";



const Scores = () => {
    // const currentGame = JSON.parse(localStorage.getItem("game"));
    
    const currPlayer1 = JSON.parse(localStorage.getItem("player1"));
    const [player1, setPlayer1] = useState(currPlayer1 || {id: 1, name: ""});

    const currPlayer2 = JSON.parse(localStorage.getItem("player2"));
    const [player2, setPlayer2] = useState(currPlayer2 || {id: 2, name: ""});

    const currPlayer3 = JSON.parse(localStorage.getItem("player3"));
    const [player3, setPlayer3] = useState(currPlayer3 || {id: 3, name: ""});

    const currPlayer4 = JSON.parse(localStorage.getItem("player4"));
    const [player4, setPlayer4] = useState(currPlayer4 || {id: 4, name: ""});

    const currAllScores = JSON.parse(localStorage.getItem("allScores"));
    const [allScores, setAllScores] = useState(currAllScores || []);

    ///////////////////////////
    const currAllIndicators = JSON.parse(localStorage.getItem("allIndicators"));
    const [allIndicators, setAllIndicators] = useState(currAllIndicators || []);

    ///////////////////////////
    const currAllPenalties = JSON.parse(localStorage.getItem("allPenalties"));
    const [allPenalties, setAllPenalties] = useState(currAllPenalties || []);
    
    const currHandNum = JSON.parse(localStorage.getItem("handNum"));
    const [handNum, setHandNum] = useState(currHandNum || 0);

    // const currScores = JSON.parse(localStorage.getItem("scores"));
    // const [scores, setScores] = useState(currScores || []);

    // const currSuit = JSON.parse(localStorage.getItem("suit"));
    // const [suit, setSuit] = useState(currSuit || {suitName: "hearts", value: "5", color: "red"});

    const currHandStorage = JSON.parse(localStorage.getItem("currHand"));
    const [currHand, setCurrHand] = useState(currHandStorage || {
        suit: {
            name: "choose",
            normalWinnerPlayerDecrement: 0,
            normalOpponentMultiplyer: 0,
            opponentMultiplyer: 0,
            kafaWinnerPlayerDecrement: 0,
            kafaOpponentMultiplyer: 0,
            color: ""
        },
        scores: ["", "", "", ""],
        indicators: [0, 0, 0, 0],
        penalties: ["", "", "", ""],
    });

    const currTotalScores = JSON.parse(localStorage.getItem("totalScores"));
    const [totalScores, setTotalScores] = useState(currTotalScores || [
        {
            id: 1,
            name: player1,
            score: ""
        },
        {
            id: 2,
            name: player2,
            score: ""
        },
        {
            id: 3,
            name: player3,
            score: ""
        },
        {
            id: 4,
            name: player4,
            score: ""
        }
    ]);

    const currLeaderBoard = JSON.parse(localStorage.getItem("leaderBoard"));
    const [leaderBoard, setLeaderBoard] = useState(currLeaderBoard || []);

    useEffect(() => {
        localStorage.setItem("handNum", JSON.stringify(handNum));
    }, [handNum]);
    
    useEffect(() => {
        localStorage.setItem("player1", JSON.stringify(player1));
    }, [player1]);
    
    useEffect(() => {
        localStorage.setItem("player2", JSON.stringify(player2));
    }, [player2]);
    
    useEffect(() => {
        localStorage.setItem("player3", JSON.stringify(player3));
    }, [player3]);

    useEffect(() => {
        localStorage.setItem("player4", JSON.stringify(player4));
    }, [player4]);

    // useEffect(() => {
    //     localStorage.setItem("scores", JSON.stringify(scores));
    // }, [scores]);

    useEffect(() => {
        localStorage.setItem("currHand", JSON.stringify(currHand));
    }, [currHand]);
    
    useEffect(() => {
        localStorage.setItem("allScores", JSON.stringify(allScores));
    }, [allScores]);

    /////////////////////
    useEffect(() => {
        localStorage.setItem("allIndicators", JSON.stringify(allIndicators));
    }, [allIndicators]);

    ////////////////////
    useEffect(() => {
        localStorage.setItem("allPenalties", JSON.stringify(allPenalties));
    }, [allPenalties]);

    useEffect(() => {
        localStorage.setItem("totalScores", JSON.stringify(totalScores));
    }, [totalScores]);
    
    useEffect(() => {
        localStorage.setItem("leaderBoard", JSON.stringify(leaderBoard));
    }, [leaderBoard]);


    useEffect(() => {
        let player1TotalScore = 0;
        allScores.forEach(
            _scores => {
                return player1TotalScore += parseInt((_scores[0] === "") ? 0 : _scores[0]);
            }
        );

        allIndicators.forEach(
            _indicators => {
                return player1TotalScore += parseInt((_indicators[0] === "") ? 0 : _indicators[0]);
            }
        );

        allPenalties.forEach(
            _penalties => {
                return player1TotalScore += parseInt((_penalties[0] === "") ? 0 : _penalties[0]);
            }
        );
        
        let player2TotalScore = 0;
        allScores.forEach(
            _scores => player2TotalScore += parseInt((_scores[1] === "") ? 0 : _scores[1])
        );

        allIndicators.forEach(
            _indicators => {
                return player2TotalScore += parseInt((_indicators[1] === "") ? 0 : _indicators[1]);
            }
        );

        allPenalties.forEach(
            _penalties => {
                return player2TotalScore += parseInt((_penalties[1] === "") ? 0 : _penalties[1]);
            }
        );

        let player3TotalScore = 0;
        allScores.forEach(
            _scores => player3TotalScore += parseInt((_scores[2] === "") ? 0 : _scores[2])
        );

        allIndicators.forEach(
            _indicators => {
                return player3TotalScore += parseInt((_indicators[2] === "") ? 0 : _indicators[2]);
            }
        );

        allPenalties.forEach(
            _penalties => {
                return player3TotalScore += parseInt((_penalties[2] === "") ? 0 : _penalties[2]);
            }
        );

        let player4TotalScore = 0;
        allScores.forEach(
            _scores => player4TotalScore += parseInt((_scores[3] === "") ? 0 : _scores[3])
        );

        allIndicators.forEach(
            _indicators => {
                return player4TotalScore += parseInt((_indicators[3] === "") ? 0 : _indicators[3]);
            }
        );

        allPenalties.forEach(
            _penalties => {
                return player4TotalScore += parseInt((_penalties[3] === "") ? 0 : _penalties[3]);
            }
        );

        setTotalScores(() => {
            return [
                {
                    id: player1.id,
                    name: player1.name,
                    score: player1TotalScore
                        || (player2TotalScore && player3TotalScore && player4TotalScore && "0")
                        || ""
                },
                {
                    id: player2.id,
                    name: player2.name,
                    score: player2TotalScore
                    || (player1TotalScore && player3TotalScore && player4TotalScore && "0")
                    || ""
                },
                {
                    id: player3.id,
                    name: player3.name,
                    score: player3TotalScore
                    || (player1TotalScore && player2TotalScore && player4TotalScore && "0")
                    || ""
                },
                {
                    id: player4.id,
                    name: player4.name,
                    score: player4TotalScore
                    || (player1TotalScore && player2TotalScore && player3TotalScore && "0")
                    || ""
                },
            ];
        });      
    }, [allScores, allIndicators, allPenalties, player1, player2, player3, player4]);

    useEffect(() => {
        const tempTotalScores = [...totalScores];
        const sortedTotalScores = tempTotalScores.sort(
            (a, b) => a.score - b.score
        ); 
        setLeaderBoard(() => sortedTotalScores);
    }, [totalScores]);

    // useEffect(() => {
    //     localStorage.setItem("suit", JSON.stringify(suit));
    // }, [suit])

    const handleSetPlayer1 = (e) => {
        setPlayer1(() => ({id: 1, name: e.target.value}));
    }
    
    const handleSetPlayer2 = (e) => {
        setPlayer2(() => ({id: 2, name: e.target.value}));
    }
    
    const handleSetPlayer3 = (e) => {
        setPlayer3(() => ({id: 3, name: e.target.value}));
    }

    const handleSetPlayer4 = (e) => {
        setPlayer4(() => ({id: 4, name: e.target.value}));
    }

    const handleSelectSuit = (e) => {
        let normalWinnerPlayerDecrement = 0;
        let normalOpponentMultiplyer = 0;
        let kafaWinnerPlayerDecrement = 0;
        let kafaOpponentMultiplyer = 0;

        switch (e.target[e.target.selectedIndex].id) {
            case "hearts":
                normalWinnerPlayerDecrement = -100;
                normalOpponentMultiplyer = 5;
                kafaWinnerPlayerDecrement = -500;
                kafaOpponentMultiplyer = 10
                break;
            case "diamonds":
                normalWinnerPlayerDecrement = -100;
                normalOpponentMultiplyer = 4;
                kafaWinnerPlayerDecrement = -400;
                kafaOpponentMultiplyer = 8
                break;
            case "spades":
                normalWinnerPlayerDecrement = -100;
                normalOpponentMultiplyer = 3;
                kafaWinnerPlayerDecrement = -300;
                kafaOpponentMultiplyer = 6
                break;
            case "clubs":
                normalWinnerPlayerDecrement = -100;
                normalOpponentMultiplyer = 2;
                kafaWinnerPlayerDecrement = -200;
                kafaOpponentMultiplyer = 4
                break;
            default:
                normalWinnerPlayerDecrement = 0;
                normalOpponentMultiplyer = 0;
                kafaWinnerPlayerDecrement = 0;
                kafaOpponentMultiplyer = 0;
                break;
        }
        const tempCurrHand = {...currHand}
        tempCurrHand.suit.name = e.target[e.target.selectedIndex].id;
        tempCurrHand.suit.normalWinnerPlayerDecrement = normalWinnerPlayerDecrement;
        tempCurrHand.suit.normalOpponentMultiplyer = normalOpponentMultiplyer;
        tempCurrHand.suit.kafaWinnerPlayerDecrement = kafaWinnerPlayerDecrement;
        tempCurrHand.suit.kafaOpponentMultiplyer = kafaOpponentMultiplyer;
        tempCurrHand.suit.color = e.target[e.target.selectedIndex].dataset.color;
        setCurrHand(() => tempCurrHand);
    }

    const addScore = () => {
        for (let i = 0; i < currHand.scores.length; i++) {
            if (currHand.scores[i] === "" || currHand.suit.name === "choose") {
                return;
            }
        }

        const tempScores = [...currHand.scores];

        currHand.scores.forEach(
            (score, i) => {
                if (currHand.scores.indexOf("-") > -1) {
                    if (score === "-") {
                        tempScores[i] = currHand.suit.normalWinnerPlayerDecrement;
                    } else {
                        tempScores[i] = tempScores[i] * currHand.suit.normalOpponentMultiplyer;
                    }
                }
                if (currHand.scores.includes("x") || currHand.scores.includes("X")) {
                    if (score.toLowerCase() === "x") {
                        tempScores[i] = currHand.suit.kafaWinnerPlayerDecrement;
                    } else {
                        tempScores[i] = tempScores[i] * currHand.suit.kafaOpponentMultiplyer;
                    }
                }
                if (currHand.scores.includes("xx") || currHand.scores.includes("XX")) {
                    if (score.toLowerCase() === "xx") {
                        tempScores[i] = 2 * currHand.suit.kafaWinnerPlayerDecrement;
                    } else {
                        tempScores[i] = 2 * tempScores[i] * currHand.suit.kafaOpponentMultiplyer;
                    }
                }
                
            }
        );
         
        setAllScores(prevScores => [...prevScores, tempScores]);
        setAllIndicators(prevIndicators => [...prevIndicators, currHand.indicators]);
        setAllPenalties(prevPenalties => [...prevPenalties, currHand.penalties]);

        setCurrHand(() => (
            {
                suit: {
                    name: "choose",
                    normalWinnerPlayerDecrement: 0,
                    normalOpponentMultiplyer: 0,
                    opponentMultiplyer: 0,
                    kafaWinnerPlayerDecrement: 0,
                    kafaOpponentMultiplyer: 0,
                    color: ""
                },
                scores: ["", "", "", ""],
                indicators: [0, 0, 0, 0],
                penalties: ["", "", "", ""],
            }
        ));
    }
    
    const isAllX = (someStr) => {
        const splStr = someStr.split("");
        for (let i = 0; i < splStr.length; i++) {
            if (splStr[i].toLowerCase() !== "x") {
                return [false, splStr.length];
            }
        }
        return [true, splStr.length];
    }
    
    const isAllDash = (someStr) => {
        const splStr = someStr.split("");
        for (let i = 0; i < splStr.length; i++) {
            if (splStr[i].toLowerCase() !== "-") {
                return [false, splStr.length];
            }
        }
        return [true, splStr.length];
    }

    const xCount = (str) => {
        let count = 0;
        for (let i = 0; i < str.length; i++) {
            if (str[i].toLowerCase() === "x") count++;
        }
        return count;
    }
    
    const dashCount = (str) => {
        let count = 0;
        for (let i = 0; i < str.length; i++) {
            if (str[i].toLowerCase() === "-") count++;
        }
        return count;
    }
    
    const handleCurrScores = (e) => {
        if (
            isNaN(e.target.value)
            && !isAllX(e.target.value)[0]
            && e.target.value[0] !== "-"
        ) return;
        if (e.target.value[0] === "-" && e.target.value.length > 1) return;
        if (
            !isNaN(e.target.value)
            && (parseInt(e.target.value) === 0)
        ) return;
        if (isAllX(e.target.value) && xCount(e.target.value.toLowerCase()) > 2) return;
        if (isAllDash(e.target.value) && dashCount(e.target.value.toLowerCase()) > 1) return;

        for (let i = 0; i < currHand.scores.length; i++) {
            if (i !== parseInt(e.target.dataset.i)) {
                if (
                    (currHand.scores[i].toLowerCase().includes("x")
                    || currHand.scores[i].toLowerCase().includes("-"))
                    && e.target.value.toLowerCase().includes("x"))
                    return;
            }
        }
        
        for (let i = 0; i < currHand.scores.length; i++) {
            if (i !== parseInt(e.target.dataset.i)) {
                if (
                    (currHand.scores[i].toLowerCase().includes("-") 
                    ||currHand.scores[i].toLowerCase().includes("x")) 
                    && e.target.value.toLowerCase().includes("-")) 
                    return;
            }
        }
        
        const tempCurrHand = {...currHand};
        tempCurrHand.scores[e.target.dataset.i] = e.target.value.toUpperCase();
        setCurrHand(tempCurrHand);
    }


    //////////////////////////////////////////////////////////////////////////////
    const handleCurrIndicator = (e) => {
        const value = -currHand.suit.normalOpponentMultiplyer * 10;
        const i = e.target.dataset.i;
        const tempCurrHand = {...currHand};
        tempCurrHand.indicators = [0, 0, 0, 0];
        tempCurrHand.indicators[i] = value;
        setCurrHand(tempCurrHand);
    }
    
    ////////////////////////////////////////////////////////////////////////////
    const handleCurrPenalties = (e) => {
        const tempCurrHand = {...currHand};
        const i = e.target.dataset.i;
        if (isNaN(e.target.value)) return;
        const value = parseInt(e.target.value);
        tempCurrHand.penalties[i] = value;
        setCurrHand(tempCurrHand);
    }

    const handleRowScores = (e) => {
        const value = e.target.value;
        // if (isNaN(value) && (value.length < 2 && value[0]!== "-")) return;
        // if (
        //     !isNaN(value)
        //     && (parseInt(value) === 0)
        // ) return;
        if (value.indexOf(" ") !== -1) return;
        if (value[0] === "0") return;
        if (
            isNaN(value)
            && (value[0] !== "-")
        ) return;
        if (value.length > 1 && isNaN(value)) return;

        // if (isNaN(value[1])) return;

        // if (value[1] === "-") return;

        // if (value[0] === "-" && isNaN(value[1])) return;

        // if (e.target.value[0] === "-" && e.target.value.length > 1) return;
        const tempAllScores = [...allScores];
        tempAllScores[e.target.dataset.i][e.target.dataset.j] = e.target.value;
        setAllScores(tempAllScores);
    }

    const handleCellOnBlur = (e) => {
        const tempAllScores = [...allScores];
        (e.target.value === "-") && (tempAllScores[e.target.dataset.i][e.target.dataset.j] = "");
        setAllScores(tempAllScores);
    }
    
    const addNewHand = () => {
        setHandNum(handNum + 1);
        setAllScores([...allScores, ["", "", "", ""]]);
    }

    const clearAllScores = () => {
        setAllScores(() => []);
        setAllIndicators(() => []);
        setAllPenalties(() => []);
        setCurrHand(prevCurrHand => ({
            ...prevCurrHand,
            indicators: [0, 0, 0, 0],
            penalties: ["", "", "", ""]
        }));
        setTotalScores(() => [
            {
                id: player1.id,
                name: player1.name,
                score: ""
            },
            {
                id: player2.id,
                name: player2.name,
                score: ""
            },
            {
                id: player3.id,
                name: player3.name,
                score: ""
            },
            {
                id: player4.id,
                name: player4.name,
                score: ""
            },
        ]);
    }

    const clearAll = () => {
        // localStorage.clear();
        setAllScores(() => []);
        setAllIndicators(() => []);
        setAllPenalties(() => []);
        setTotalScores(() => [
            {
                id: player1.id,
                name: player1.name,
                score: ""
            },
            {
                id: player2.id,
                name: player2.name,
                score: ""
            },
            {
                id: player3.id,
                name: player3.name,
                score: ""
            },
            {
                id: player4.id,
                name: player4.name,
                score: ""
            },
        ]);

        setCurrHand(() => (
            {
                suit: {
                    name: "choose",
                    normalWinnerPlayerDecrement: 0,
                    normalOpponentMultiplyer: 0,
                    opponentMultiplyer: 0,
                    kafaWinnerPlayerDecrement: 0,
                    kafaOpponentMultiplyer: 0,
                    color: ""
                },
                scores: ["", "", "", ""],
                indicators: [0, 0, 0, 0],
                penalties: ["", "", "", ""],
            }
        ));
        setPlayer1(() => ({id: 1, name: ""}));
        setPlayer2(() => ({id: 2, name: ""}));
        setPlayer3(() => ({id: 3, name: ""}));
        setPlayer4(() => ({id: 4, name: ""}));
    }

    const clearCurrIndicators = () => {
        const tempCurrHand = {...currHand};
        tempCurrHand.indicators = [0, 0, 0, 0];
        setCurrHand(tempCurrHand);
    }

    const clearCurrPenalties = () => {
        const tempCurrHand = {...currHand};
        tempCurrHand.penalties = ["", "", "", ""];
        setCurrHand(tempCurrHand);
    }

    const mapPlaces = (placeNum) => {
        let place = "";
        switch (placeNum) {
            case 1:
                place = "first";
                break;
            case 2:
                place = "second";
                break;
            case 3:
                place = "third";
                break;
            case 4:
                place = "fourth";
                break;
        
            default:
                place = "none";
                break;
        }
        return place;
    }


    return ( 
        <article className="table-container">
            <table className="table">
                <thead className="players">
                    <tr>
                        <td ></td>
                        <td id="player1-name" data-name="player1" className="player">
                            <input
                                type="text"
                                name="player1"
                                id="player1" 
                                onChange={handleSetPlayer1}
                                placeholder="Oyuncu 1"
                                value={player1.name}
                                className={
                                    ((totalScores[0].score !== "")
                                    && mapPlaces(leaderBoard.map(el => el.id).indexOf(player1.id) + 1))
                                    || ""
                                }
                            /> 
                        </td>
                        <td id="player2-name" data-name="player2" className="player">
                            <input
                                type="text"
                                name="player2"
                                id="player2" 
                                onChange={handleSetPlayer2}
                                placeholder="Oyuncu 2"
                                value={player2.name}
                                className={
                                    ((totalScores[1].score !== "")
                                    && mapPlaces(leaderBoard.map(el => el.id).indexOf(player2.id) + 1))
                                    || ""
                                }
                            /> 
                        </td>
                        <td id="player3-name" data-name="player3" className="player">
                            <input
                                type="text"
                                name="player3"
                                id="player3" 
                                onChange={handleSetPlayer3}
                                placeholder="Oyuncu 3"
                                value={player3.name}
                                className={
                                    ((totalScores[2].score !== "") 
                                    && mapPlaces(leaderBoard.map(el => el.id).indexOf(player3.id) + 1))
                                    || ""
                                }
                            /> 
                        </td>
                        <td id="player4-name" data-name="player4" className="player">
                            <input
                                type="text"
                                name="player4"
                                id="player4" 
                                onChange={handleSetPlayer4}
                                placeholder="Oyuncu 4"
                                value={player4.name}
                                className={
                                    ((totalScores[3].score !== "") 
                                    && mapPlaces(leaderBoard.map(el => el.id).indexOf(player4.id) + 1))
                                    || ""
                                }
                            /> 
                        </td>
                        <td></td>
                    </tr>
                </thead>
                <tbody className="scores">
                    {allScores.map((row_scores, index) =>
                        <ScoreRow
                            key={index}
                            row_scores={row_scores}
                            allScores={allScores}
                            hand={index + 1}
                            i={index}
                            handleRowScores={handleRowScores}
                            handleCellOnBlur={handleCellOnBlur}
                        />
                    )}
                    <tr className="total-scores">
                        <td>
                            {/* <Trophy /> */}
                            <Medal />
                        </td>
                        {/* <td>üèÜ</td> */}
                        {totalScores.map((el, i) =>
                            <td
                                className={`player${i + 1}-total-score`}
                                key={i}
                            >
                                {el.score}
                            </td>
                        )}
                        <td></td>
                    </tr>
                    <tr className="empty-row">
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr className="add-scores">
                        <td id="suit">
                            <select
                                name="suit"
                                id="select-suit"
                                onChange={handleSelectSuit}
                                style={{color: `${currHand.suit.color}`}}
                                value={currHand.suit.name}
                            >
                                {/* <option id="choose" value="choose" data-color="black">
                                    &#xf078;
                                </option> */}
                                <option id="choose" value="choose" data-color="black">üîª</option>
                                {/* <option id="choose" value="choose" data-color="black">üÇ°</option> */}
                                {/* <option id="choose" value="choose" data-color="black">
                                    <ChevronDown />
                                </option> */}
                                <option id="hearts" value="hearts" data-color="red">‚ô•Ô∏èÔ∏è</option>
                                <option id="diamonds" value="diamonds" data-color="red">‚ô¶Ô∏è</option>
                                <option id="spades" value="spades" data-color="black">‚ô†Ô∏è</option>
                                <option id="clubs" value="clubs" data-color="black">‚ô£Ô∏è</option>
                            </select>
                            
                        </td>
                        
                        {currHand.scores.map(
                            (score, i) => <td className="curr-hand_player-score" key={i}>
                                <input
                                    type="text"
                                    // inputMode="numeric"
                                    // pattern="[0-9]*"
                                    name={`player${i + 1}-curr-score`}
                                    id={`player${i + 1}-curr`}
                                    value={score}
                                    data-i={i}
                                    onChange={handleCurrScores}
                                />
                            </td>
                        )}
                        <td>
                            <button
                                className="btn btn-add-score-row"
                                onClick={addScore}
                            >
                                {/* + */}
                                <Plus />
                            </button>
                        </td>
                    </tr>
                    <tr className="indicators">
                        <td id="gosterge">
                            {/* <span id="gosterge-g-harfi">üçí</span> */}
                            <span id="gosterge-g-harfi">G</span>
                        </td>
                        
                        {currHand.indicators.map(
                            (indicator, i) => <td key={i}>
                                <input
                                    type="radio"
                                    name="player-indicator"
                                    id={`player${i}-indicator`}
                                    value={indicator}
                                    data-i={i}
                                    onChange={handleCurrIndicator}
                                    checked={parseInt(indicator) !== 0}
                                />
                            </td>
                        )}
                        <td>
                            <button
                                className="btn btn-clear-curr-indicators"
                                onClick={clearCurrIndicators}
                            >
                                <Broom />
                            </button>
                        </td>
                    </tr>
                    <tr className="penalties">
                        <td id="penalty">
                            <span id="penalty-c-harfi">C</span>
                        </td>
                        {currHand.penalties.map(
                            (penalty, i) => <td className="curr-hand_player-penalty" key={i}>
                            <input
                                type="text"
                                // inputMode="numeric"
                                // pattern="[0-9]*"
                                name={`player${i + 1}-curr-penalty`}
                                id={`player${i + 1}-curr-penalty`}
                                value={penalty || ""}
                                data-i={i}
                                onChange={handleCurrPenalties}
                            />
                        </td>
                        )}
                        <td>
                            <button
                                className="btn btn-clear-curr-penalties"
                                onClick={clearCurrPenalties}
                            >
                                <Broom />
                            </button>
                        </td>
                    </tr>
                </tbody> 
            </table>

            <div className="buttons">
                <button
                    className="btn btn-new-row"
                    onClick={addNewHand}
                >
                    Bo≈ü El &nbsp;
                    <PlusSquare />
                </button>
                <button
                    className="btn btn-clear-all-scores"
                    onClick={clearAllScores}
                >
                    Skorlarƒ± &nbsp;
                    <Trash />

                </button>
                <button
                    className="btn btn-clear-all"
                    onClick={clearAll}
                >
                    Her≈üeyi &nbsp;
                    <Trash />
                </button>
            </div> 
        </article>
    );
}

export default Scores;